import React, {useEffect, useState} from "react";
import './App.css';
import {css, cx} from "@emotion/css";
import {Question, questionsList} from "./Questions";
import {useMediaQuery} from "@material-ui/core";

const App: React.FC = () => {

    let [selectedIndex, setIndex] = useState(0);
    let [questions, setQuestions] = useState([] as Question[]);
    let [counterIndex, setCounterIndex] = useState(0);
    let [answerLabel, setAnswerLabel] = useState("Odpowiedz")

    useEffect(() => {
        setQuestions(questionsList);
    }, []);

    function getNext(): void {
        setIndex(++selectedIndex);
        setAnswerLabel("Odpowiedz")
        setTimeout(function () {
            setCounterIndex(++counterIndex)
            if (selectedIndex === questions.length) {
                setIndex(0);
                setCounterIndex(0)
            }
        }, animationTime);
    }

    function showAnswer(): void {
        let text = questions[selectedIndex].isTrue ? "Prawda" : "Fałsz"
        setAnswerLabel(text)
    }

    let height = 360;
    const animationTime = 200;

    function SetHeightIfMobile() {
        if (useMediaQuery('(max-height:700px)')) {
            height = 200;
        }
        if (useMediaQuery('(max-height:400px)')) {
            height = 100;
        }
    }

    SetHeightIfMobile();

    const counterStyle = css({
        transition: `opacity ${animationTime}ms ease`,
        opacity: 0,
        position: "absolute",
        left: 0,
        top: height + 160,
        bottom: 0,
        right: 0
    });

    const answerStyle = css({
        transition: `opacity ${animationTime}ms ease`,
        opacity: 0,
        position: "absolute",
        left: 0,
        top: height + 80,
        bottom: 0,
        right: 0
    });

    const questionStyle = css({
        transition: `opacity ${animationTime}ms ease`,
        opacity: 0,
        position: "absolute",
        left: 0,
        top: height ,
        bottom: 0,
        right: 0
    });

    const topicStyle = css({
        transition: `opacity ${animationTime}ms ease`,
        opacity: 0,
        position: "absolute",
        left: 0,
        top: height - 40,
        bottom: 0,
        right: 0
    });

    const buttonAnswerStyle = css({
        transition: `opacity ${animationTime}ms ease`,
        opacity: 0,
        position: "absolute",
        left: 0,
        top: height + 120,
        bottom: 0,
        right: 250
    });

    const buttonNextStyle = css({
        transition: `opacity ${animationTime}ms ease`,
        opacity: 0,
        position: "absolute",
        left: 250,
        top: height + 120,
        bottom: 0,
        right: 0
    });

    const selectedTextStyle = css({
        transition: `opacity ${animationTime}ms ease ${animationTime}ms`,
        opacity: 1
    });

    return (
        <header
            className="App-header"
            style={{
                display: "grid",
                gridTemplateColumns: "1fr",
                gridTemplateRows: "3em auto",
                justifyItems: "center"
            }}
        >
            <div className="App" style={{position: "relative", width: "100vw"}}>
                {questions.map((singleQuestion, index) => (
                    <>
                        <div
                            className={cx(
                                topicStyle,
                                index === selectedIndex && selectedTextStyle
                            )}
                        >
                            {singleQuestion.topic}
                        </div>
                        <div
                            className={cx(
                                questionStyle,
                                index === selectedIndex && selectedTextStyle
                            )}
                        >
                            {singleQuestion.question}
                        </div>
                        <div
                            className={cx(
                                answerStyle,
                                index === selectedIndex && selectedTextStyle
                            )}
                        >
                            {singleQuestion.answer}
                        </div>
                        <div onClick={() => showAnswer()}
                            className={cx(
                                buttonAnswerStyle,
                                index === selectedIndex && selectedTextStyle
                            )}>
                            {answerLabel}
                        </div>
                        <div onClick={() => getNext()}
                            className={cx(
                                buttonNextStyle,
                                index === selectedIndex && selectedTextStyle
                            )}>
                            Nastepne
                        </div>
                        <div
                            className={cx(
                                counterStyle,
                                index === selectedIndex && selectedTextStyle
                            )}>
                            {counterIndex + 1}/{questions.length}
                        </div>
                    </>
                ))}
            </div>
        </header>
    );
};

export default App;
